import { post } from '@/tool/http.js'
import qs from 'qs'
const header={headers: {'Content-Type': 'application/x-www-form-urlencoded'}}
// 搜索列表
export const searchList = params => post('/travelApp/busLineSite/searchList',qs.stringify(params),header)

// 实时公交
export const selectLineCars = params =>post(`/travelApp/busLineSite/selectLineCars/${params.userId}`,params.lineSiteCarsParams)

// 线路附近站点列表
export const nearSites = params => post('/travelApp/busLineSite/nearSites',qs.stringify(params),header)

// 推荐线路
export const recommendLine = params =>post(`/travelApp/busLineSite/recommendLine`,qs.stringify(params),header)

// 地点详情
export const SiteDetails = params =>post('/travelApp/busLineSite/selectSiteDetails',params)

// 车辆相信信息
export const selectSiteDetails = params =>post('/travelApp/busLineSite/selectLineDetails',qs.stringify(params))