import React, { Component } from 'react'
import Searchbar from '@/components/searchBar/index'
import List from '@/components/list/index'
import bus2 from '@/assets/imgs/bus2.png'
import station from '@/assets/imgs/station.png'
import cir from '@/assets/imgs/cir.png'
import { searchList } from '@/tool/api/real_bus.js'
// import { Toast } from 'antd-mobile';
import './index.scss'
export default class Search extends Component {
   constructor(props){
       super(props)
       const { log,lat } = props.location.state
       this.state={
           lineBus:[],
           lineImg:[
               {id:0,text:'线路',img:bus2},
               {id:1,text:'车站',img:station},
               {id:2,text:'地点',img:cir}],
           log:log,
           lat:lat
       }
   }
    _returndata = async ( value )=>{
        let { log , lat } = this.state
        let params = {
            userId: 'osxt55ExUUB53CVwvZ3v2h0xwAPw',
            keyword: value,
            longitude: log,
            latitude: lat}
        let {data,code} = await searchList(params)
        if(code!==200) return
        data=data.filter(item=>item.lineType==='realtime_bus')
        this.setState({
            lineBus:data
        })
    }
    _changePage = (lineId)=>{
        this.props.history.push({pathname:'/details',state:{lineId}})
    }
    render() {
        const { lineBus , lineImg } = this.state
        return (
            <div className="search">
                <Searchbar returnback={this._returndata} source={false}></Searchbar>
                {
                    lineBus.length
                    ?<div className="search_list">
                        <div className="list_top">搜索结果</div>
                        <List listBus={lineBus}></List>
                    </div>
                    :<div className="search_null">
                        <div className="null_text">您可以搜索</div>
                        <div className="null_img">
                            {
                                lineImg.map( item =>{
                                    return (
                                        <div className="img_line" key={item.id}>
                                            <img src={item.img} alt=""/>
                                            <span>{item.text}</span>
                                        </div>
                                    )
                                })
                            }
                        </div>
                    </div>
                }
            </div>
        )
    }
}
