import React, { Component } from 'react'
import {connect} from 'react-redux'//引入redux容器组件
import { withRouter } from 'react-router-dom'
import { CITY } from '@/redux/city_location/variable.js'//引入reducers变量
// import { debounce } from '@/tool/location.js'
import search from '@/assets/imgs/search.png'
import './index.scss'
class Searchbar extends Component {
    _changePage = () => {
        const { source ,mylocation } = this.props
        if( !source ) return 
        this.props.history.push({pathname:'/search',state:{log:mylocation.longitude,lat:mylocation.latitude}})
    }
    _changeData = (e) => {
        const { data } = e.nativeEvent
        this.props.returnback(data)
    }
    render() {
        const { source } = this.props
        return (
            <div className="index_search">
                <div className="search_box" onClick={this._changePage}>
                    {source?<span>公交</span>:''}
                    <img src={search} alt="" />
                    {
                        source
                        ?<div>输入公交线路、车站、地点</div>
                        :<input 
                            type="text" 
                            placeholder="输入公交线路、车站、地点"
                            onChange={e=>this._changeData(e)}
                            ></input>
                            // onChange={debounce(this._changeData,500)}
                    }
                </div>
            </div>
        )
    }
}

export default connect(
    (state)=>({mylocation:state.locationCity}),
    (dispatch)=>({
        one:(number)=>{
            dispatch({type:CITY,data:number})
        }
    })
)(withRouter( Searchbar ))