import React, { Component , Fragment } from 'react'
import { withRouter } from 'react-router-dom'
import {connect} from 'react-redux'//引入redux容器组件
import { CITY } from '@/redux/city_location/variable.js'//引入reducers变量
import bus2 from '@/assets/imgs/bus2.png'
import station from '@/assets/imgs/station.png'
import cir from '@/assets/imgs/cir.png'
import './index.scss'
class List extends Component {
    _changePage = (id,type) =>{
        const { mylocation } = this.props
        if(type==='realtime_bus'){
            this.props.history.push({pathname:'/details',state:{log:mylocation.longitude,lat:mylocation.latitude,lineId:id}})
        }
    }
    render() {
        const { listBus } = this.props
        return (
            <Fragment>
                {
                    listBus.map(item=>{
                        return (
                            <div className="list" key={item.id} onClick={e=>this._changePage(item.id,item.lineType)}>
                                <div className="list_left">
                                   { item.type==='line'?<img src={cir} alt=""/>:item.type==='site'?<img src={station} alt=""/>
                                   :<img src={bus2} alt=""/>}
                                </div>
                                <div className="list_right">
                                    <span>{item.name}</span>
                                    <span className="right_span">{item.country}</span>
                                </div>
                            </div>
                        )
                    })
                }
            </Fragment>
        )
    }
}
export default connect(
    (state)=>({mylocation:state.locationCity}),
    (dispatch)=>({
        one:(number)=>{
            dispatch({type:CITY,data:number})
        }
    })
)(withRouter( List ))
